header{
    position: fixed;
    top:0;
    left: 0;
    z-index: 999;
    height: 45px;
    width: 100%;
    display:flex;
    align-items:center;
    justify-content:space-between;
    background-color: transparent;
}
header a {
    text-decoration: none;
    color: inherit;
    padding: 3px;
    margin: 10px;
    font-weight: bold;
    border-radius: 1px;
    text-align: center;
    color: #111;
    cursor: pointer;
}
header img{
    width: 70px;
    margin: auto 6px;
    box-sizing: border-box;
}

.escolher{
    position: fixed;
    top:0;
    left: 120px;
    z-index: 999;
    height: 40px;
    display:flex;
    align-items:center;
    justify-content:center;
}
.escolher button{
    margin: auto 5px;
    padding: 5px 10px;
    background-color: rgb(173, 10, 10);
    color: #fff;
    font-weight: bold;
    border: none;
    cursor: pointer;
}

.escolher button:active{
    background-color: rgb(10, 173, 108);
}
