iframe{
    width: 100%;
    height: 99.3vh;
    object-fit: fill;
    overflow: hidden;
    padding: 0px;
    margin: 0px;
    z-index: 998;

}
.Comands{
    position: fixed;
    top:0;
    right: 10px;
    z-index: 999;
    height: 40px;
    display:flex;
    align-items:center;
    justify-content:center;
}
.Comands button{
    margin: auto 5px;
    padding: 5px 10px;
    background-color: rgb(173, 10, 10);
    color: #fff;
    font-weight: bold;
    border: none;
    cursor: pointer;
}

@media screen and (max-device-width: 650px) and (orientation: portrait) {
    iframe {
        position: absolute;
        transform: rotate(90deg);
        transform-origin: bottom left;
        width: 100vh;
        height: 100vw;
        padding: 0px;
        margin: -100vw 0px 0px 0px;
        object-fit: cover;
        top: 0px;
        left: 0px;
    }
}